<template>
  <div>
    <!-- ÁªüËÆ°Ë°®Ê†º -->
    <div
      v-if="charts.length > 0"
      class="grid pt-3 pb-6"
    >
      <div
        class="bg-semi radius base-shadow overflow-hidden"
        v-for="(it, i) in charts"
        :key="i"
      >
        <area-chart
          :statistic="it.statistic"
          :label="it.label"
          :color="it.color"
          :options="it.options"
          :series="it.series"
        >
          <feather
            slot="icon"
            :class="it.color"
            :type="it.icon"
          ></feather>
        </area-chart>
      </div>
    </div>

    <!-- È´òÁ∫ßÊêúÁ¥¢ -->
    <div class="bg-white rounded-lg">
    </div>

    <!-- ËÆ¢ÂçïÂàóË°® -->
    <div
      id="table-loading"
      class="radius vs-con-loading__container"
    >
      <vs-table
        search
        pagination
        max-items="10"
        noDataText="ÊöÇÊó†Êï∞ÊçÆ"
        :data="orderList"
      >
        <template slot="header">
          <div class="w-full flex items-center p-4">
            <div class="text-xl text-primary font-semibold">ËÆ¢ÂçïÂàóË°®</div>
            <div class="ml-auto mr-4 text-sm text-semi">
              ÂÖ±Êü•ËØ¢Âà∞ {{ pagination.total }} Êù°Êï∞ÊçÆ
            </div>
            <div class="">
              <el-date-picker
                type="daterange"
                align="right"
                unlink-panels
                v-model="date"
                start-placeholder="ÂºÄÂßãÊó•Êúü"
                end-placeholder="ÁªìÊùüÊó•Êúü"
                :picker-options="pickerOptions"
                @change="onDateChange"
              ></el-date-picker>
            </div>
          </div>
        </template>
        <template slot="thead">
          <vs-th>ÂïÜÂìÅÂêçÁß∞</vs-th>
          <vs-th>‰π∞ÂÆ∂</vs-th>
          <vs-th>ÂçñÂÆ∂</vs-th>
          <vs-th>ÊÄª‰ª∑</vs-th>
          <vs-th>ÂàõÂª∫Êó∂Èó¥</vs-th>
          <vs-th>ÊîØ‰ªòÊñπÂºè</vs-th>
          <vs-th>Áä∂ÊÄÅ</vs-th>
          <vs-th></vs-th>
        </template>

        <template slot-scope="{data}">
          <template v-for="order in data">
            <template v-for="sub in order.sub_order">
              <vs-tr
                v-for="td in sub.goods_list"
                :key="td._id"
                @dblclick.native="viewDetail(td._id)"
              >
                <vs-td>{{ td.goods.name }}</vs-td>
                <vs-td>
                  <span
                    class="cursor-pointer"
                    @click="$router.push({
                    path: '/user-detail',
                    query: {userId: td.goods.buyer._id},
                  })"
                  >{{ td.goods.buyer.nickname }}</span>
                </vs-td>
                <vs-td>
                  <span
                    class="primary cursor-pointer"
                    @click="$router.push({
                    path: '/user-detail',
                    query: {userId: td.goods.seller._id},
                  })"
                  >@{{ td.goods.seller.nickname }}</span>
                </vs-td>
                <vs-td class="font-bold">
                  Ôø•{{ Number(td.goods.price).toFixed(2) }}
                </vs-td>
                <vs-td :title="$dayjs(td.goods.created_at).format('YYYY/MM/DD HH:mm:ss')">
                  {{ $timeDiff(td.goods.created_at) }}
                </vs-td>
                <vs-td class="font-bold">{{ payments[order.payment] }}</vs-td>
                <vs-td>
                  <vs-chip
                    :style="{background: `rgba(var(--vs-${status[sub.status].color}), 0.15)`}"
                    :class="['font-bold', status[sub.status].color]"
                  >
                    {{ status[sub.status].text }}
                  </vs-chip>
                </vs-td>
                <vs-td>
                  <vs-dropdown>
                    <i class="el-icon-more px-2 text-lg text-gray"></i>
                    <vs-dropdown-menu>
                      <vs-dropdown-item>
                        <div
                          class="w-24 text-center"
                          @click="onCopy(sub._id)"
                        >Â§çÂà∂ÂçïÂè∑</div>
                      </vs-dropdown-item>
                      <vs-dropdown-item>
                        <div
                          class="w-24 text-center"
                          @click="viewOrderDetail(order._id, sub._id)"
                        >Êü•ÁúãËØ¶ÊÉÖ</div>
                      </vs-dropdown-item>
                      <vs-dropdown-item divider>
                        <div
                          class="w-24 danger text-center"
                          @click="deleteOrder(order._id)"
                        >Âà†Èô§ËÆ¢Âçï</div>
                      </vs-dropdown-item>
                    </vs-dropdown-menu>
                  </vs-dropdown>
                </vs-td>
              </vs-tr>
            </template>
          </template>
        </template>
      </vs-table>
    </div>
  </div>
</template>

<script>
import AreaChart from '@/components/AreaChart.vue'
import { areaChartOptions } from '@/utils/util'

import {
  getOrderList,
  getOrderListByDateRange,
  getOrderTransactionAmount,
  getOrderVolume,
  getOrderNum,
} from '@/request/api/order'

const gridCharts = [
  {
    statistic: 3200, label: '‰∫§ÊòìÈ¢ù', color: 'primary', icon: 'dollar-sign',
  },
  {
    statistic: 800, label: 'Êàê‰∫§Èáè', color: 'warning', icon: 'check-circle',
  },
  {
    statistic: 1900, label: 'ËÆ¢ÂçïÊï∞', color: 'success', icon: 'bar-chart-2',
  },
]
const payments = {
  huabei: '‰ΩôÈ¢ùÊîØ‰ªò',
  weixin: 'ÂæÆ‰ø°ÊîØ‰ªò',
  zhifubao: 'ÊîØ‰ªòÂÆùÊîØ‰ªò',
  yinlian: 'Èì∂Ë°åÂç°ÊîØ‰ªò',
}
const status = {
  1: { text: 'ËøõË°å‰∏≠', color: 'primary' },
  2: { text: 'Â∑≤ÂÆåÊàê', color: 'success' },
  3: { text: 'Ê¥æÈÄÅ‰∏≠', color: 'warning' },
  4: { text: 'Â∑≤ÂèñÊ∂à', color: 'danger' },
}
const pickerOptions = {
  shortcuts: [{
    text: 'ÊúÄËøë‰∏ÄÂë®',
    onClick(picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      picker.$emit('pick', [start, end])
    },
  }, {
    text: 'ÊúÄËøë‰∏Ä‰∏™Êúà',
    onClick(picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
      picker.$emit('pick', [start, end])
    },
  }, {
    text: 'ÊúÄËøë‰∏â‰∏™Êúà',
    onClick(picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
      picker.$emit('pick', [start, end])
    },
  }],
}
export default {
  name: 'OrderList',
  components: { AreaChart },

  data: () => ({
    status,
    payments,
    pickerOptions,

    charts: [], // Âç°ÁâáÁªüËÆ°
    date: [], // Êó•ÊúüËåÉÂõ¥
    orderList: [], // ËÆ¢ÂçïÂàóË°®
    pagination: {}, // ÂàÜÈ°µ‰ø°ÊÅØ
    searchText: '',
  }),

  activated() {
    this.initCharts()
    this.getOrderList()
  },

  methods: {
    async initCharts() {
      const colors = ['#6165f7', '#FF9F43', '#5DC76F']
      const res = await Promise.all(
        [getOrderTransactionAmount(), getOrderVolume(), getOrderNum()],
      )
      this.charts = gridCharts.map((el, i) => {
        const options = this.$cloneDeepWith(areaChartOptions)
        options.colors = [colors[i]]
        return Object.assign(el, { options }, { series: [res[i].data] })
      })
    },

    async getOrderList() {
      this.$vs.loading({
        type: 'point',
        container: '#table-loading',
        scale: 1,
      })

      try {
        const { code, data } = await getOrderList({
          page: 1,
          page_size: 100,
        })
        if (code === 2000) {
          this.orderList = data.order_list
          this.pagination = data.pagination
        }
      } finally {
        this.$vs.loading.close('#table-loading > .con-vs-loading')
      }
    },

    onSearch() {
      this.getOrderList()
    },

    // ÊåâÊó•ÊúüËé∑ÂèñÂïÜÂìÅ
    async onDateChange(date) {
      this.$vs.loading({
        type: 'point',
        container: '#table-loading',
        scale: 1,
      })
      if (date) {
        try {
          const date_range = date.map(el => this.$dayjs(el).format('YYYY-MM-DD'))
          const { code, data } = await getOrderListByDateRange({
            date_range,
            page: 1,
            page_size: 100,
          })
          if (code === 2000) {
            this.orderList = data.order_list
            this.pagination = data.pagination
          }
        } finally {
          this.$vs.loading.close('#table-loading > .con-vs-loading')
        }
      } else {
        this.getOrderList()
      }
    },

    // Êü•ÁúãÂïÜÂìÅËØ¶ÊÉÖ
    viewOrderDetail(orderId, subId) {
      this.$router.push({
        path: '/order-detail',
        query: { orderId, subId },
      })
    },

    deleteOrder(id) {
      this.orderList.forEach((el, i, _) => {
        if (el._id === id) {
          _.splice(i, 1)
        }
      })
    },

    onCopy(id) {
      this.$copyText(id).then(() => {
        this.$message(`Â∑≤Â§çÂà∂ËÆ¢ÂçïÁºñÂè∑Ôºö${id}  üéâ`)
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 20px;
}
</style>
